h1 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 800;
  font-size: 30px;
  color: #2C3652; }
  @media screen and (max-width: 768px) {
    h1 {
      font-size: 20px; } }

h2 {
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  font-size: 30px;
  color: #2C3652; }
  @media screen and (max-width: 768px) {
    h2 {
      font-size: 20px; } }

h3 {
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
  font-size: 18px;
  color: #2C3652; }
  @media screen and (max-width: 768px) {
    h3 {
      font-size: 14px; } }

h5 {
  font-family: 'Open Sans', sans-serif;
  font-weight: normal;
  font-size: 12px;
  color: #6E7F95; }

h6 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 14px;
  color: #6E7F95; }
  @media screen and (max-width: 768px) {
    h6 {
      font-size: 10px; } }

.h7 {
  font-family: 'Open Sans', sans-serif;
  font-weight: 600;
  font-size: 12px;
  color: #B6BDCB; }

button {
  border: none;
  padding: 0;
  background: transparent;
  transition: all 0.1s ease; }
  button:hover {
    cursor: pointer; }
  button:active {
    transform: scaleY(1.1); }
  button:focus, button:active {
    outline: none; }

.noscroll {
  overflow: hidden; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  overflow-x: hidden !important; }
  body .App {
    overflow-y: visible !important; }

hr {
  background-color: #c8e0e4; }

a {
  color: #00D7AF; }
  a:hover {
    color: #36B29B; }

.no-padding,
.container-fluid {
  padding: 0; }

.main-container-fluid {
  padding-top: 60px;
  overflow: hidden; }
  @media screen and (min-width: 802px) {
    .main-container-fluid {
      padding-top: 84px; } }

.form-seperator {
  margin-top: 34px;
  font-size: 24px;
  font-weight: bold;
  color: #354F53; }

@media screen and (min-width: 576px) {
  .form-right-padding-small,
  .form-right-padding {
    padding-right: 10px !important; }
  .form-left-padding-small,
  .form-left-padding {
    padding-left: 10px !important; } }

@media screen and (min-width: 1040px) {
  .form-right-padding {
    padding-right: 20px !important; }
  .form-left-padding {
    padding-left: 20px !important; } }

.modal-backdrop {
  background-image: linear-gradient(134deg, rgba(0, 215, 175, 0.75), rgba(70, 115, 122, 0.75)); }
  .modal-backdrop.show {
    opacity: 0.75; }

.modal-open {
  overflow: hidden !important; }

.modal-dialog {
  box-shadow: 0 30px 40px 0 rgba(0, 0, 0, 0.15);
  border-radius: 8px; }
  .modal-dialog .modal-content {
    padding: 10px 40px 40px 40px;
    border: none; }
    @media screen and (max-width: 576px) {
      .modal-dialog .modal-content {
        padding: 5px 15px 15px 15px; } }

.input-label {
  min-height: 18px;
  font-weight: bold;
  font-size: 12px;
  color: #354F53;
  margin: 0;
  margin-left: 7px;
  transform: translateY(8px); }
  @media screen and (max-width: 576px) {
    .input-label {
      min-height: 0; } }

.cashback-earned-container {
  margin: auto; }
  .cashback-earned-container p,
  .cashback-earned-container span {
    font-weight: 600;
    font-size: 12px;
    color: #6480EB;
    margin-left: 5px; }

.blue-modal-backdrop {
  background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
  opacity: 0.9; }

.flow-visible {
  opacity: 1;
  transition: opacity 0.2s ease; }

.flow-invisible {
  opacity: 0;
  transition: opacity 0.2s ease; }

.alice-carousel {
  max-width: calc(100vw - 30px); }
