.go-to-app-on-mobile {
  padding-top: 60px; }
  .go-to-app-on-mobile > .container {
    background-color: #36B29B;
    padding: 20px 18px 15px 18px;
    max-width: unset; }
    @media screen and (max-width: 375px) {
      .go-to-app-on-mobile > .container {
        padding: 22px 14px 15px 14px; } }
  .go-to-app-on-mobile .btn-redirect {
    min-width: 90px;
    min-height: unset !important;
    width: auto !important;
    height: auto !important;
    padding: 8px 12px; }
    .go-to-app-on-mobile .btn-redirect:hover, .go-to-app-on-mobile .btn-redirect:focus {
      outline: none; }
    @media screen and (max-width: 375px) {
      .go-to-app-on-mobile .btn-redirect {
        min-width: unset !important;
        padding: 7px 12px; } }
    .go-to-app-on-mobile .btn-redirect > p {
      white-space: nowrap; }
    @media screen and (max-width: 375px) {
      .go-to-app-on-mobile .btn-redirect .text {
        display: none; } }
    .go-to-app-on-mobile .btn-redirect .icon {
      font-size: 1.2rem; }
      @media screen and (min-width: 376px) {
        .go-to-app-on-mobile .btn-redirect .icon {
          display: none; } }
  .go-to-app-on-mobile [class^="app"] {
    margin-bottom: 0;
    color: #fff; }
  .go-to-app-on-mobile .app-icon {
    padding: 10px;
    background-color: #fff;
    border-radius: 12px; }
  .go-to-app-on-mobile .app-name {
    font-weight: bold;
    font-size: 1.2rem; }
  .go-to-app-on-mobile .app-desc {
    font-size: 1rem; }
    @media screen and (max-width: 375px) {
      .go-to-app-on-mobile .app-desc {
        font-size: 0.875rem; } }
  .go-to-app-on-mobile .close {
    position: absolute;
    text-shadow: none;
    font-weight: normal;
    font-size: 1rem;
    color: #fff;
    background-color: #46737A;
    padding: 3px 5px;
    border-bottom-right-radius: 12px; }
