.loading-screen {
  background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0; }
  .loading-screen .spinner-animation {
    top: calc(45% - 40px);
    width: 60px !important;
    height: 60px !important; }
    .loading-screen .spinner-animation .dot1,
    .loading-screen .spinner-animation .dot2 {
      width: 30px !important;
      height: 30px !important;
      background-color: #F2FAF9 !important; }
  .loading-screen .dot1, .loading-screen .dot2 {
    background-color: #F2FAF9 !important; }
  .loading-screen .bottom-container {
    position: absolute;
    top: 50%;
    width: 230px;
    left: calc(50% - 115px);
    overflow-x: hidden;
    overflow-y: hidden; }
    .loading-screen .bottom-container p {
      font-weight: bold;
      font-size: 12px;
      color: white;
      text-align: center;
      margin: 0; }
  .loading-screen .logo-container {
    display: flex; }
    .loading-screen .logo-container img {
      height: 64px;
      width: 64px;
      margin: 0 10px; }

.loading-active {
  opacity: 0.9;
  transition: opacity 0.3s ease; }

.loading-inactive {
  opacity: 0;
  transition: opacity 0.3s ease; }

.loading-error {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 480px;
  text-align: center; }
  .loading-error .error-text-main {
    font-size: 24px;
    font-weight: bold;
    color: #354F53; }
  .loading-error .error-text-extra {
    font-size: 18px;
    font-weight: 600;
    color: #354F53;
    margin: 20px 0; }
  .loading-error .error-text-try {
    font-size: 16px;
    font-weight: normal;
    color: #99BBC1;
    margin-bottom: 20px;
    transform: translateY(-20px); }
